@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    @apply bg-slate-200 p-3 select-none;
}

.board {
    @apply gap-2 sm:gap-3 md:gap-4 lg:gap-5 grid grid-flow-col auto-cols-auto;
}

.list {
    @apply w-full min-w-max;
}

.list-title {
    @apply bg-blue-500 text-white font-bold py-2 px-4 shadow-lg;
}

.card-container {
    @apply bg-gray-100 text-slate-600 py-2 px-4 mt-1 shadow-lg rounded;
}

.add-card-container {
    @apply bg-gray-200 hover:bg-gray-300 hover:cursor-pointer text-slate-700 py-2 px-4 mt-1 shadow-lg rounded;
}

.close-icon-list {
    @apply float-right hover:text-gray-300 hover:cursor-pointer ml-3;
}

.close-icon-card {
    @apply float-right hover:text-black hover:cursor-pointer ml-3;
}

.close-icon-modal {
    @apply float-right text-slate-600  hover:text-black hover:cursor-pointer ml-3;
}

.top-button {
    @apply bg-blue-500 hover:bg-blue-700 hover:cursor-pointer text-white font-bold py-2 px-4 shadow-lg min-w-max;
}

.modal {
    @apply fixed hidden z-40 w-screen h-screen inset-0 bg-gray-900 bg-opacity-60;
}

.modal-content {
    @apply fixed z-50 top-1/4 left-1/2 -translate-x-1/2 -translate-y-1/2 w-1/2 bg-white rounded-md px-8 py-6 drop-shadow-lg;
}

label {
    @apply text-gray-700 text-lg font-bold mb-2;
}

input[type=text] {
    @apply shadow border w-full rounded py-2 px-3 mb-1 text-gray-700 leading-tight focus:outline-none;
}

input[type=submit] {
    @apply bg-blue-500 hover:bg-blue-700 hover:cursor-pointer text-white font-bold py-1.5 px-3 rounded focus:outline-none;
}